						content: '请登录后再操作',
						confirmText:'马上登录',
						success: function (res) {
							if (res.confirm) {
								uni.navigateTo({
								    url: '../login/login',
								});
							} else if (res.cancel) {
								
							}
						}
					});
					return false;
				}
				if (!this.text) {
					uni.showToast({
						title: '请输入评论内容',
						icon: 'none'
					});
					return false;
				}
				var self = this;
// 				self.replylist.unshift({
