					userId:self.id,
					type:self.type,
					targetId:self.banner.id,
					parentId:0,
					rootId:0,
					parentUserId:0,
					commentContent:self.text
				};
				
				uni.request({
					url:this.websiteUrl+'uniApp/comment/addComment',
					data: data,
					success: (result) => {
						//评论成功，才重新请求comment列表
						if(result.data.status == 1){
							self.getCommentList();
						}else{
							uni.showToast({
								 title:  result.data.message,
								duration: 2000
							});
						}
					}
				})
				
